import Creator from "images/creator.webp";
import Paragraph from "components/interface/Paragraph";

const AuthorSection = () => {
  return (
    <article className="article">
      <Paragraph
        heading="Who is the creator?"
        description={[
          "Hello, Im Szymon Hyziak - 19 years high school graduate from Poland. Ive been developing websites for over a year and Im thrilled to show you another one",
          "Ive got a background in IT, thats related to my high school. I also fancy UI Design and photography.",
          "If you want to contact me work-wise dont hesitate to reach me through LinkedIn. All the useful links are below!",
        ]}
        className="biography"
      />
      <img
        src={Creator}
        alt="Headshot of the creator - Szymon Hyziak."
        className="creator"
      />
    </article>
  );
};

export default AuthorSection;
